@import "src/sass/variables";

.descriptionBlock {
  display: flex;
  /*background-color: $purpleColor2;
  border-radius: 4px;*/

  .photoBlock {
    display: flex;
    flex-direction: column;
    border-radius: 4px;
    width: 260px;
    align-items: center;
    justify-content: center;

    .mainPhoto {
      margin: 10px 0;
      height: 240px;
      width: 240px;
      border-radius: 4px;
    }

    .inputFile {
      margin-bottom: 10px;
      margin-left: 12px;
      &::-webkit-file-upload-button {
        background: $orangeColor;
        color: white;
        height: 26px;
        border-radius: 4px;
        padding: 3px;
      }
    }
  }

  .profileData {
    border-radius: 4px;
    margin-left: 10px;
    width: 100%;
    padding: 10px 0;

    .name {
      font-size: large;
    }
  }
}